// follower n following
.followWrapper {
  position: relative;
  padding: 5px 0 16px 0;
  width: 622px;
  height: 100%;
  border-bottom: 1px solid #e6ecf0;
  

  .followCollection {
    height: 100%;
    display: flex;
    margin-left: 24px;

    .avatarWrap {
      margin-top: 10px;
      margin-right: 5px;
      position: relative;
      width: 56px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &:hover {
        cursor: pointer;
      }
    }

    .followContent {
      width: 580px;
      height: 100%;
      display: flex;
      flex-direction: column;
      margin-left: 8px;

      .nameInfo {
        width: 95%;
        height: 100%;
        display: flex;
        line-height: 26px;
        font-size: 16px;
        font-weight: 700;
        margin-right: 8px;
        margin-top: 18px;
      }

      .userIntroduction {
        width: 95%;
        height: 100%;
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        margin-top: 8px;
        color: #171725;
      }

      .followIcon {
        position: absolute;
        right: 30px;
        top: 15px;
      }
    }
  }
}

// UserInfo
.container {
  width: 639px;
  height: 100%;
  // border: 1px solid #e6ecf0;
  // border-bottom: none;
  // border: 1px solid #000;

  .header {
    height: 75px;
    margin-left: 28px;
    display: flex;
