// UserReplyList
.userNav {
  height: 52px;
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 15px;
  font-weight: 700;

  div {
    width: 130px;
    line-height: 52px;
    text-align: center;
  }

  .active {
    border-bottom: 2px solid #ff6600;
    color: #ff6600;
  }
}

// UserReplyTweet
.tweetContainer {
  width: 100%;
  min-height: 148px;
  border: 1px solid #e6ecf0;
  border-top: none;
  // border: 1px solid #000;
  padding-top: 17px;
}

.tweetCollection {
  display: flex;
  margin-left: 23.13px;

  .logoWrap {
    position: relative;
    width: 50px;
    height: 50px;
    background-color: rgb(189, 188, 188);
    border-radius: 50%;

    &:hover {
      cursor: pointer;
    }

    .darkLogo {
      position: absolute;
      top: 9px;
      left: 9px;
      width: 34px;
      height: 34px;
      opacity: 0.3;
    }
  }

  .tweetContent {
    width: 100%;
    height: 118px;
    display: flex;
    flex-direction: column;
    margin-left: 8px;

    .nameInfo {
      position: relative;
      display: flex;
      line-height: 26px;
      align-items: center;
      margin-bottom: 8px;

      .tweetName {
        font-size: 16px;
        font-weight: 700;
        margin-right: 8px;
      }

      .tweetTime {
        font-size: 14px;
        color: #6c757d;
      }
    }

    .reply {
      margin-top: 8px;
      color: #6c757d;

      .replyTo {
        margin-left: 5px;
        color: #ff6600;
      }
    }

    .tweetArticle {
      width: 90%;
      margin-top: 8px;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      color: #171725;
    }

    .count {
      margin-top: 9.49px;
      display: flex;

      div {
        display: flex;
        gap: 9.44px;
        align-items: center;
      }

      .likeNum {
        margin-left: 41.3px;
      }
    }
  }
}

