    height: 22px;
    font-family: "Noto Sans TC";
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: #6c757d;

    &:hover {
      cursor: pointer;
    }
  }
}

.otherNumWrapper {
  width: 100%;
  height: 22px;
  display: flex;
  flex-direction: row;
  gap: 20px;
  margin-top: 8px;

  div {
    min-width: 75px;
    height: 22px;
    font-family: "Noto Sans TC";
    font-style: normal;
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: #171725;

    span {
      color: #6c757d;
    }
  }
}

